<template>
  <div class="view-style" id="game-main-id">
      <div class="back-style" id="back-style-id" :style="`background-image:url(${require('@/assets/image/main_back/main_image_'+time_back+'_a.png')})`"></div>
      <!-- <video muted loop class="back-style" id="back-style-id" :src="`${require('@/assets/video/sky.mp4')}`"></video> -->
      <div class="surface-picture" id="surface-picture-id">
          <audio class="audio-style" :src="`${require('@/assets/audio/Zombies On Your Lawn.mp3')}`" preload="auto" loop id="audio" ref="audio" muted></audio>
          <div class="play-music-btn" id="play-music-btn-id" @click="audioEvent" :style="`color:${color}`" :title="musicName">M</div>
          <div class="title-style" data-content="Good Night">Good Night , My Town</div>
          <div class="play-game-btn" id="play-game-btn-id" :style="`left:${btn.left1}px`" @click="newGame">新游戏</div>
          <div class="continue-game-btn" id="continue-game-btn-id" :style="`left:${btn.left1}px`" @click="archiveShow">继续游戏</div>
          <div class="explain-style">行动说明：{{message}}</div>

          <!-- 存档页面 -->
          <div class="archive-style" v-if="archiveItemShow" id="archive-id">
              <div class="archive-item-style" v-for="item in archiveData" :key="item.id" id="archive-item-id">
                  <div class="archive-item-title-style" :id="`archive-item-title-id${item.id}`" @contextmenu="contextmenu($event,item)" @click="selectArchive(item)">{{item.title}}</div>
                  <div class="vice-archive" :id="`vice-archive-id${item.id}`">上次游玩:({{item.updataDate}})</div>
                  <!-- <div class="archive-item-title-style" :id="`archive-item-title-id${item.id}`" @click="selectArchive(item)">{{item.title}}</div> -->
              </div>
          </div>
      </div>
      <ul class="ulstyle" id="ul">
          <li class="listyle" @click="delArchive">删除此存档("{{preDelItem.title}}")</li>
      </ul>
  </div>
</template>

<script>
import * as TweenMax from 'gsap/all'
import _ from 'lodash'
import { encryptLong, decryptLong } from '@/utils/jsencrypt';
import {setArchive,parseTime} from '@/api/common';

export default {
    name:"GameView",
    data(){
        return{
            btn:{
                left1:0
            },
            color:"crimson",
            isPlaying:false,
            musicName:"Zombies On Your Lawn",
            message:"™40℃！！！！！",
            archiveItemShow:false,
            archiveData:[],
            time_back:2,
            preDelItem:{},
        }
    },
    mounted(){
        this.clac_dom();
        this.getArchive();
        // this.getTime();
        // this.$nextTick(()=>{
        //     document.getElementById('back-style-id').play();
        // })
        document.getElementById('game-main-id').onclick = ()=>{
            document.getElementById('ul').style.display = 'none'
        }
        document.getElementById('game-main-id').oncontextmenu = (e)=>{
            e.preventDefault()
        }
    },
    methods:{
        // 按钮样式调整
        clac_dom(){
            var dom1 = document.getElementById("surface-picture-id");
            var dom2 = document.getElementById("play-game-btn-id");

            var width1 = dom1.offsetWidth;
            var width2 = dom2.offsetWidth;

            this.btn.left1 = (width1 / 2) - (width2 / 2);
        },
        // 播放按钮样式调整
        audioEvent(){
            if(this.isPlaying == true){
                this.$refs.audio.pause();
                this.$refs.audio.currentTime = 0
                this.isPlaying = false;
                this.color = "crimson";
            }else{
                this.$refs.audio.play();
                this.isPlaying = true;
                this.color = "honeydew";
            }
            // console.d(this.$refs.audio);
        },
        // 播放bgm
        palyAudio(){
            var mp3 = require("@/assets/audio/button04a.mp3")
            this.audio = new Audio();
            this.audio.src = mp3;
            this.audio.play();
        },
        // 存档展示
        archiveShow(){
            this.getArchive();
            if(!localStorage.getItem('archive') && document.getElementById('archive-item-title-id1')){
                alert("存档遭到破坏，需要重启！")
                return location.reload();
            }
            if(this.archiveData.length == 0){
                return alert("没有存档！")
            }
            if(this.archiveItemShow == false){
                this.archiveItemShow = true;
                this.$nextTick(()=>{
                    this.setDom();
                    // TweenMax.gsap.fromTo("#archive-id",1,{border:"2px groove rgba(240, 255, 240, 0)",borderRadius:0},{border:"2px groove rgba(240, 255, 240, 1)",borderRadius:10});
                    TweenMax.gsap.fromTo("#archive-item-id",0.2,{backgroundColor:"rgba(240, 255, 240, 0)"},{backgroundColor:"rgba(240, 255, 240, 0.5)"});
                })
            }else{
                TweenMax.gsap.fromTo("#archive-item-id",0.2,{backgroundColor:"rgba(240, 255, 240, 0.5)"},{backgroundColor:"rgba(240, 255, 240, 0)",onComplete:(()=>{this.archiveItemShow = false})});
                if(this.archiveData.length > 0){
                    this.archiveData.forEach((item)=>{
                        TweenMax.gsap.fromTo(`#archive-item-title-id${item.id}`,0.2,{color:"rgb(44, 62, 80)"},{color:"rgba(44, 62, 80, 0)"});
                    })
                }
            }
        },
        // 存档字体样式调整
        setDom(){
            if(this.archiveData.length > 0){
                this.archiveData.forEach((item)=>{
                    var dom1 = document.getElementById("archive-item-title-id" + item.id);
                    // var dom2 = document.getElementById("vice-archive-id" + item.id);
                    dom1.style.lineHeight = dom1.offsetHeight + 'px';
                    // dom2.style.top = '17%';
                })
            }
        },
        // 新建存档
        newGame(){
            this.archiveItemShow = false;
            if(!localStorage.getItem('archive') && this.archiveData.length > 0){
                alert("存档遭到破坏，需要重启！")
                return location.reload();
            }
            if(this.archiveData.length == 5){
                alert('存档数量已达上限，无法新建！');
            }else{
                var title = prompt("请输入存档名称（字符长度不超过7）");
                if(title == null || title == ""){
                    return false;
                }else{
                    if(title.length > 7){
                        return alert("存档标题长度超出7！") && false;
                    }
                    setArchive(this.archiveData.length + 1,title)
                    // title.length > 7 ? alert("存档标题长度超出7！") : setArchive(this.archiveData.length + 1,title)
                    this.getArchive();
                    this.archiveShow();
                }
                // setArchive(this.archiveData.length + 1,)
            }
        },
        // 继续游玩上一次存档
        continueGame(){
            if(this.archiveData.length == 0){
                alert("没有存档！");
            }
        },
        // 获取存档列表
        getArchive(){
            this.archiveData = [];
            if(!localStorage.getItem("archive")){
                return '';
            }
            // console.log(localStorage.getItem("archive"));
            const list = JSON.parse(decryptLong(localStorage.getItem("archive")));
            list.forEach((item,index)=>{
                this.archiveData.push(item)
            })
            // console.log(this.archiveData);
        },
        async delArchive(){
            await this.archiveData.splice(this.archiveData.indexOf(this.preDelItem))
            localStorage.setItem("archive", encryptLong(JSON.stringify(this.archiveData)))
            document.getElementById('ul').style.display = 'none'
            // console.log(this.archiveData.indexOf(this.preDelItem));
        },
        // 将选择后的存档放入vuex
        async selectArchive(e){
            await this.$store.dispatch("addArchive", e)
            this.$router.push({name:"MainGame"})
        },
        contextmenu(e,item){
            this.preDelItem = item
            e.preventDefault()
            var ul = document.getElementById('ul')
            ul.style.display = 'block',
            ul.style.top = e.clientY+'px'
            ul.style.left = e.clientX+'px'
            // console.log(item);
        },
        // getTime(){
        //     const time = Number(parseTime(new Date(),'HH'))
        //     // console.log(time);
        //     if(_.inRange(time,6,8)){
        //         this.time_back = 3
        //         console.log('清晨');
        //     }
        //     if(_.inRange(time,8,12)){
        //         this.time_back = 3
        //         console.log('上午');
        //     }
        //     if(_.inRange(time,12,13)){
        //         this.time_back = 3
        //         console.log('中午');
        //     }
        //     if(_.inRange(time,13,17)){
        //         this.time_back = 3
        //         console.log('下午');
        //     }
        //     if(_.inRange(time,17,20)){
        //         this.time_back = 3
        //         console.log('傍晚');
        //     }
        //     if(_.inRange(time,20,23) || _.inRange(time,0,6)){
        //         this.time_back = 3
        //         console.log('晚上');
        //     }
        // },
    }
}
</script>

<style lang="scss" scoped>
.view-style{
    width: 100%;
    height: 100%;
}
.title-style{
    width: 100%;
    position: absolute;
    z-index: 10;
    top: 150px;
    font-size: 150px;
    color: transparent;
    // background: linear-gradient(rgb(217,126,95) 60%,rgba(103,166,125));
    // background-clip: text;
    font-family: "Cooper", Courier, monospace;
    -webkit-text-stroke: 3px rgb(103,166,125);
    text-shadow: 0px 2px rgba(240, 255, 240, 0.5);
}
.back-style{
    position: absolute;
    z-index: 1;
    // top: 0%;
    // left: 0%;
    width: 100%;
    height: 100%;
}
.audio-style{
    width: 100px;
    height: 100px;
}
.explain-style{
    width: 100%;
    height: 50px;
    line-height: 50px;
    position: absolute;
    z-index: 10;
    top: calc(100% - 50px);
    color: honeydew;
    font-family: "幼圆", Courier, monospace;
}
.archive-style{
    position: absolute;
    z-index: 10;
    width: 30%;
    height: 60%;
    bottom: 5%;
    left: 5%;
    // border-radius: 10px;
}
.archive-item-style{
    width: 98%;
    height: 18%;
    margin-top: 1%;
    margin-bottom: 1%;
    margin-left: 1%;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.2s;
}
.archive-item-style:hover{
    transform: scale(1.01,1.01);
    // box-shadow : 0 2px 5px rgba(0, 0, 0, 0.49), 0 3px 8px rgba(0, 0, 0, 0.40) ;
}
.archive-item-title-style{
    width: 100%;
    height: 90%;
    font-family: "幼圆", Courier, monospace;
    font-size: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    letter-spacing: 3px;
}
.vice-archive{
    // position: absolute;
    // z-index: 10;
    width: 100%;
    // height: 30px;
    height: 10%;
    line-height: 0px;
    font-size: 15px;
    font-family: "幼圆", Courier, monospace;
    text-align: center;
    letter-spacing: 3px;
    // left: 1%;
    // top: 17%;
    // margin-bottom: 50%;
}
.surface-picture{
    position: absolute;
    z-index: 10;
    width: 100%;
    height: 100%;
}
.play-game-btn{
    position: absolute;
    z-index: 10;
    top: 45%;
    width: 10%;
    height: 50px;
    border: 5px groove rgba(245, 245, 245, 0.5);
    border-radius: 10px;
    text-align: center;
    line-height: 50px;
    letter-spacing: 5px;
    font-size: 25px;
    color: honeydew;
    cursor: pointer;
    font-family: "幼圆", Courier, monospace;
    transition: 0.1s all;
}
.play-game-btn:active{
    background-color: rgba(240, 255, 240, 0.3);
}
.play-game-btn:hover{
    font-size: 30px;
}
.archive-btn{
    position: absolute;
    z-index: 10;
    top: calc(45% + 200px);
    width: 10%;
    height: 50px;
    border: 5px groove rgba(245, 245, 245, 0.5);
    border-radius: 10px;
    text-align: center;
    line-height: 50px;
    letter-spacing: 5px;
    font-size: 25px;
    color: honeydew;
    cursor: pointer;
    font-family: "幼圆", Courier, monospace;
    transition: 0.1s all;
}
.archive-btn:active{
    background-color: rgba(240, 255, 240, 0.3);
}
.archive-btn:hover{
    font-size: 30px;
}
.continue-game-btn{
    position: absolute;
    z-index: 10;
    top: calc(45% + 100px);
    width: 10%;
    height: 50px;
    border: 5px groove rgba(245, 245, 245, 0.5);
    border-radius: 10px;
    text-align: center;
    line-height: 50px;
    letter-spacing: 5px;
    font-size: 25px;
    color: honeydew;
    cursor: pointer;
    font-family: "幼圆", Courier, monospace;
    transition: 0.1s all;
}
.continue-game-btn:active{
    background-color: rgba(240, 255, 240, 0.3);
}
.continue-game-btn:hover{
    font-size: 30px;
}
.play-music-btn{
    position: absolute;
    z-index: 10;
    top: 5px;
    right: 5px;
    width: 50px;
    height: 50px;
    border: 5px groove rgba(245, 245, 245, 0.5);
    border-radius: 10px;
    text-align: center;
    line-height: 50px;
    letter-spacing: 5px;
    font-size: 30px;
    // color: honeydew;
    cursor: pointer;
    font-family: "Ink Free", Courier, monospace;
    transition: 0.2s all;
}
.play-music-btn:active{
    background-color: rgba(240, 255, 240, 0.3);
}
.play-music-btn:hover{
    font-size: 40px;
}
.ulstyle{
    display: none;
    position: fixed;
    z-index: 999;
    list-style: none;
}
.listyle{
    background-color: rgba(240, 255, 240, 0.8);
    border-radius: 5px;
    font-family: "幼圆", Courier, monospace;
    // width: 100px;
    height: 30px;
    line-height: 30px;
    cursor: pointer;
}
.listyle:hover{
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.30);
}
</style>