import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import { encryptLong, decryptLong } from '@/utils/jsencrypt';
import {updataArchive} from '@/api/common'

//axios
import axios from 'axios'
Vue.prototype.$axios = axios
axios.defaults.baseURL='http://127.0.0.1:9301';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

Vue.config.productionTip = false

router.beforeEach((to,from,next)=>{
  if(to.name == "game"){
    updataArchive()
    sessionStorage.removeItem('Temporary_Archive')
    next()
  }else{
    if(store.state.archive.archive.id){
      sessionStorage.setItem('Temporary_Archive',encryptLong(JSON.stringify(store.state.archive.archive)))
    }else{
      store.dispatch("addArchive",JSON.parse(decryptLong(sessionStorage.getItem('Temporary_Archive'))))
    }
    next()
  }
})

new Vue({
  axios,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
