import { encryptLong, decryptLong } from '@/utils/jsencrypt';
import _ from 'lodash'
import store from '@/store'
import router from '@/router'

// 检测localStorage中archive值状态
export function init() {
    if (!localStorage.getItem("archive")) {
        localStorage.setItem("archive", encryptLong(JSON.stringify([])))
    }
}
// 新增存档
export function setArchive(id, title) {
    // init()
    var list = [];
    const newArchive = {
        id: id++,//唯一标识
        lv: '告密者',//等级--阶位
        lvdegree:[1,2,3],//等级晋升条件
        title: title,//存档标题
        property: { //币种
            type_1: 99999,//光点
            type_2: 99999,//樱饼
        },
        own_cards:[],//解锁的卡牌
        unlock_background:[1],//解锁的卡牌背景
        unlock_trim:[],//解锁的卡牌装饰
        createDate: parseTime(new Date(),'YYYY-mm-dd HH:MM'),//创建日期时间
        updataDate: parseTime(new Date(),'YYYY-mm-dd HH:MM'),//更改日期时间
    };
    // var list = JSON.parse(decryptLong(localStorage.getItem("archive")));
    if(!localStorage.getItem("archive")){
        // console.log(encryptLong(JSON.stringify([newArchive])));
        localStorage.setItem("archive", encryptLong(JSON.stringify([newArchive])))
    }else{
        list = JSON.parse(decryptLong(localStorage.getItem("archive")))
        list.push(_.cloneDeep(newArchive))
        localStorage.setItem("archive", encryptLong(JSON.stringify(list)))
    }
    // store.dispatch("addArchive", list);
    // router.push({name:"MainGame"})
}
// 更新存档
export function updataArchive() {
    if(!sessionStorage.getItem("Temporary_Archive")){
        return ;
    }
    var archive = JSON.parse(decryptLong(localStorage.getItem("archive")));
    var currentArchive = JSON.parse(decryptLong(sessionStorage.getItem("Temporary_Archive")));
    // console.log(currentArchive);
    currentArchive.updataDate = parseTime(new Date(),'YYYY-mm-dd HH:MM');
    var index = _.findIndex(archive, (e) => { return e.id == currentArchive.id });
    // console.log(index);
    localStorage.setItem('archive', encryptLong(JSON.stringify(_.fill(archive, currentArchive, index,index + 1))));
}
